export default "body {\n    font-family: 'Roboto', sans-serif;\n}\n\n.container {\n    width: 600px;\n    height: 350px;\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    display: inline-flex;\n}\n\n.backbox {\n    background-color: white;\n    width: 100%;\n    height: 80%;\n    position: absolute;\n    transform: translate(0,-50%);\n    top: 50%;\n    display: inline-flex;\n}\n\n.frontbox {\n    background-color: #60A4FF;\n    border-radius: 20px;\n    height: 100%;\n    width: 50%;\n    right: 0;\n    z-index: 10;\n    position: absolute;\n    margin-right: 3%;\n    margin-left: 3%;\n    transition: right .8s ease-in-out;\n}\n\n.moving {\n    right: 45%;\n}\n\n.center {\n    right: 21%;\n}\n\n.profileicon {\n    width: 30%;\n    position: absolute;\n    left: 35%;\n    top: 15%;\n}\n\n.loggedin {\n    text-align: center;\n}\n\n.displayname {\n    color: white;\n    top: 50%;\n    left: 0px;\n    width: 100%;\n    position: absolute;\n}\n\n.loginMsg, .signupMsg {\n    width: 50%;\n    height: 100%;\n    font-size: 15px;\n    box-sizing: border-box;\n}\n\n.loginMsg .title,\n    .signupMsg .title{\n        font-weight: 300;\n        font-size: 23px;\n    }\n\n.loginMsg p,\n    .signupMsg p {\n        font-weight: 100;\n    }\n\n.reset p{\n    margin-top: 30px;\n    color: white;\n    margin-bottom: 50px;\n}\n\n.textcontent {\n    color: #60A4FF;\n    margin-top: 65px;\n    margin-left: 12%;\n}\n\n.textcontent p {\n        color: #60A4FF;\n    }\n\n.loginMsg button,\n.signupMsg button{\n    background-color: #60A4FF;\n    border: 2px solid white;\n    border-radius: 10px;\n    color: white;\n    font-size: 12px;\n    box-sizing: content-box;\n    font-weight: 300;\n    padding: 10px;\n    margin-top: 12px;\n    margin-left: 15px;\n}\n\n.login, .signup, .reset {\n    padding: 20px;\n    text-align: center;\n}\n\n.login h2,\n    .signup h2,\n    .reset h2 {\n        color: white;\n        font-size: 22px;\n    }\n\n.inputbox {\n    margin-top: 30px;\n}\n\ninput {\n    display: block;\n    width: 100%;\n    height: 40px;\n    background-color: white;\n    border: none;\n    margin-bottom: 20px;\n    font-size: 12px;\n    border-radius: 7px;\n    padding-left: 10px;\n}\n\n.frontbutton, .leftbutton {\n    background-color: white;\n    border: none;\n    color: #60A4FF;\n    font-size: 12px;\n    font-weight: 300;\n    box-sizing: content-box;\n    padding: 10px;\n    border-radius: 10px;\n    width: 60px;\n    position: absolute;\n    right: 30px;\n    bottom: 30px;\n    cursor: pointer;\n}\n\n.leftbutton {\n    left: 30px;\n}\n\n.login p {\n    cursor: pointer;\n    color: white;\n    font-size: 15px;\n}\n\n.loginMsg, .signupMsg {\n    transition: opacity .8s ease-in-out;\n}\n\n.visibility {\n    opacity: 0;\n}\n\n.hide {\n    display: none;\n}\n\n.animated {\n    -webkit-animation-duration: 0.5s;\n    animation-duration: 0.5s;\n    -webkit-animation-fill-mode: both;\n    animation-fill-mode: both;\n}\n\n@-webkit-keyframes shake {\n    from, to {\n        transform: translate3d(0, 0, 0);\n    }\n\n    10%, 50%, 90% {\n        transform: translate3d(-10px, 0, 0);\n    }\n\n    30%, 70% {\n        transform: translate3d(10px, 0, 0);\n    }\n}\n\n@keyframes shake {\n    from, to {\n        transform: translate3d(0, 0, 0);\n    }\n\n    10%, 50%, 90% {\n        transform: translate3d(-10px, 0, 0);\n    }\n\n    30%, 70% {\n        transform: translate3d(10px, 0, 0);\n    }\n}\n\n.shake {\n    -webkit-animation-name: shake;\n    animation-name: shake;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */";